
export interface Comment {
	_id: string;
	user: string;
	text: string;
	name: string;
	avatar: string;
	date: string;
}

export interface Post {
	_id: string;
	user: string;
	text: string;
	name: string;
	avatar: string;
	likes: string[];
	comments: Comment[];
	date: string;
}

export interface PostState {
	posts: Post[];
	post: Post | null;
	isLoading: boolean;
	error: string | null;
}
export const initialPostState: PostState = {
    post: null,
    posts: [],
    isLoading: false,
    error: null,
};